import React, { Component } from "react";
import "./style/racing.css";
import RacingGameSlide from "./racing.game.slide";
import axios from "axios";
import { Card } from "react-bootstrap";
import { Link } from "react-router-dom";
export default class racinggame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      racinggames: [],
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/racegames/")
      .then((res) => {
        this.setState({ racinggames: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    console.log(this.state.racinggames);
    return (
      <div>
        <RacingGameSlide />
        <div className="bg-secondary">
        <br></br>
        <div className="container">
          <div className="row ">
            {this.state.racinggames.map((racegames) => (
              <div className="col-md-4 col-sd-4 col-lg-4 p-2" key={racegames._id}>
                <Card style={{ width: "20rem" }} className="bg-dark">
                  <Card.Img variant="top" src={racegames.imgurl} />
                  <Card.Body>
                    <Card.Title style={{color:"white", }}>{racegames.gamename}</Card.Title>
                    <Link
                      to={"/gamedetails/"+racegames._id}
                      className="btn btn-success"
                      style={{ width: "280px" }}
                    >
                      Download
                    </Link>
                  </Card.Body>
                </Card>
              </div>
            ))}
          </div>
        </div>
        </div>
      </div>
    );
  }
}
