import React, { Component } from "react";
import "./style/adventure.css";
import AdvantureGameSlide from "./advanture.game.slide";
import { Card } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom";
export default class advanturegame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      advanturegames: [],
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/advanturegames/")
      .then((res) => {
        this.setState({ advanturegames: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    return (
      <div>
        <AdvantureGameSlide />
        <div className="bg-secondary">
          <br></br>
          <div className="container">
            <div className="row ">
              {this.state.advanturegames.map((advgames) => (
                <div
                  className="col-md-4 col-sd-4 col-lg-4 p-2"
                  key={advgames._id}
                >
                  <Card style={{ width: "20rem" }} className="bg-dark">
                    <Card.Img variant="top" src={advgames.imgurl} />
                    <Card.Body>
                      <Card.Title style={{ color: "white" }}>
                        {advgames.gamename}
                      </Card.Title>
                      <Link
                        to={"/gamedetails/" + advgames._id}
                        className="btn btn-success"
                        style={{ width: "280px" }}
                      >
                        Download
                      </Link>
                    </Card.Body>
                  </Card>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
