import React, { Component } from "react";
import "./style/sports.css";
import SportsGameSlide from "./sports.game.slide";
import { Card } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom";
export default class sportsgame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sportsgames: [],
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/sportsgames/")
      .then((res) => {
        this.setState({ sportsgames: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    return (
      <div>
        <SportsGameSlide />
        <div className="bg-secondary">
        <br></br>
        <div className="container">
          <div className="row ">
            {this.state.sportsgames.map((sprgames) => (
              <div className="col-md-4 col-sd-4 col-lg-4 p-2" key={sprgames._id}>
                <Card style={{ width: "20rem" }} className="bg-dark">
                  <Card.Img variant="top" src={sprgames.imgurl} />
                  <Card.Body>
                    <Card.Title style={{color:"white", }}>{sprgames.gamename}</Card.Title>
                    <Link
                      to={"/gamedetails/"+sprgames._id}
                      className="btn btn-success"
                      style={{ width: "280px" }}
                    >
                      Download
                    </Link>
                  </Card.Body>
                </Card>
              </div>
            ))}
          </div>
        </div>
        </div>
      </div>
    );
  }
}
